.defaultInput {
    border: 2px solid grey;
    border-radius: 5px;
    font-size: 16px;
    font-weight: 400;
    outline: none;
    padding: 14px 12px;
    transition: border .2s linear;
    font-family: inherit;
}

.defaultInput:focus {
    border: 2px solid rgb(25, 118, 210);
}

.errorInput {
    border: 2px solid rgb(211, 47, 47);
}

.errorInput:focus {
    border: 2px solid rgb(211, 47, 47);
}

.error {
    color: rgb(211, 47, 47);
    display: block;
    height: 25px;
}